import React from "react";

const Sidebar = (props) => {
  return (
    <>
      <div>
        <button onClick={props.onSidebarToggle}>Toggle Sidebar</button>
      </div>
      {props.isSidebarOpen && (
        <div className={props.className}>Hello world!</div>
      )}
    </>
  );
};

export default Sidebar;
